import { IgcXRippleModule } from './igc-x-ripple-module';
import { IgcChildContentModule } from 'igniteui-core/igc-child-content-module';
import { IgcXButtonComponent } from './igc-x-button-component';
import { XButton } from './XButton';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcXButtonModule {
    public static register() {
                IgcXRippleModule.register();
        IgcChildContentModule.register();
        IgcXButtonComponent.register();
        TypeRegistrar.registerCons('IgcXButtonComponent', IgcXButtonComponent);        TypeRegistrar.registerCons('IgcXButtonComponent', IgcXButtonComponent);
        TypeRegistrar.register('XButton', (XButton as any).$type);

    }
}