import { IgcChildContentModule } from 'igniteui-core/igc-child-content-module';
import { IgcXIconComponent } from './igc-x-icon-component';
import { XIcon } from './XIcon';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcXIconModule {
    public static register() {
                IgcChildContentModule.register();
        IgcXIconComponent.register();
        TypeRegistrar.registerCons('IgcXIconComponent', IgcXIconComponent);        TypeRegistrar.registerCons('IgcXIconComponent', IgcXIconComponent);
        TypeRegistrar.register('XIcon', (XIcon as any).$type);

    }
}