import { IgcChildContentModule } from 'igniteui-core/igc-child-content-module';
import { IgcXInputComponent } from './igc-x-input-component';
import { XInput } from './XInput';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcXInputModule {
    public static register() {
                IgcChildContentModule.register();
        IgcXInputComponent.register();
        TypeRegistrar.registerCons('IgcXInputComponent', IgcXInputComponent);        TypeRegistrar.registerCons('IgcXInputComponent', IgcXInputComponent);
        TypeRegistrar.register('XInput', (XInput as any).$type);

    }
}