import { IgcXRippleModule } from './igc-x-ripple-module';
import { IgcChildContentModule } from 'igniteui-core/igc-child-content-module';
import { IgcXCheckboxComponent } from './igc-x-checkbox-component';
import { XCheckbox } from './XCheckbox';
import { TypeRegistrar } from 'igniteui-core/type';


export class IgcXCheckboxModule {
    public static register() {
                IgcXRippleModule.register();
        IgcChildContentModule.register();
        IgcXCheckboxComponent.register();
        TypeRegistrar.registerCons('IgcXCheckboxComponent', IgcXCheckboxComponent);        TypeRegistrar.registerCons('IgcXCheckboxComponent', IgcXCheckboxComponent);
        TypeRegistrar.register('XCheckbox', (XCheckbox as any).$type);

    }
}